      singleFreqCommand(){
        console.log(2/3);
          //command structure: {"id":"945102d5-94e4-448e-bbbf-48384c662711","t":1634664795,"cmd":"sq","freq":100000,"avg":1,"sparam":{"S11":true,"S12":false,"S21":true,"S22":false}}
          let params = {
            id:'',
            t:dayjs().unix(),
            freq:this.frequency,
