        let params = {
            id:'',
            t:dayjs().unix(),
            range:{Start: this.frequency, End: this.frequency_end},
            size: Number(this.size),
            isLog: this.isLog,
            avg:Number(this.avgCounts),
            sparam:{S11:this.s11,S12:this.s12,S21:this.s21,S22:this.s22}
          }

          this.$store.dispatch('requestRange', params);
      }
      
  }
}
