
<template>
<div>

    <div v-if='getSessionExpired' class='col-12'>
        <img id='session-end-image' src='https://assets.practable.io/images/common/thank-you-screen.svg' alt='session ended'>
    </div>
    <div v-else class='col-sm-12' id='overlay'>
          <img id='pvna-image' src='../../public/images/VNA.png' alt='pvna-image'>
    </div>

    <div v-if='getResponse.cmd == "sq"'>
      <div class='row'>
        <div class='input-group'>
          <span class='input-group-text'>Command</span>
          <span class='input-group-text text-primary bg-white'>{{getResponse.cmd}}</span>
        </div>
      </div>
      <div class='row'>
        <div class='input-group'>
          <span class='input-group-text'>Frequency requested</span>
          <span class='input-group-text text-primary bg-white'>{{getResponse.freq}}</span>
        </div>
      </div>
      <div v-if='getResponse.sparam.S11' class='row'>
        <div class='input-group'>
          <span class='input-group-text'>S11</span>
          <span class='input-group-text'>Real</span>
          <span class='input-group-text text-primary bg-white'> {{getResponse.result.S11.Real}} </span>
          <span class='input-group-text'>Imag</span>
          <span class='input-group-text text-primary bg-white'> {{getResponse.result.S11.Imag}} </span>
        </div>
      </div>

      <div v-if='getResponse.sparam.S12' class='row'>
        <div class='input-group'>
          <span class='input-group-text'>S12</span>
          <span class='input-group-text'>Real</span>
          <span class='input-group-text text-primary bg-white'> {{getResponse.result.S12.Real}} </span>
          <span class='input-group-text'>Imag</span>
          <span class='input-group-text text-primary bg-white'> {{getResponse.result.S12.Imag}} </span>
        </div>
      </div>

      <div v-if='getResponse.sparam.S21' class='row'>
        <div class='input-group'>
          <span class='input-group-text'>S21</span>
          <span class='input-group-text'>Real</span>
          <span class='input-group-text text-primary bg-white'> {{getResponse.result.S21.Real}} </span>
          <span class='input-group-text'>Imag</span>
          <span class='input-group-text text-primary bg-white'> {{getResponse.result.S21.Imag}} </span>
        </div>
      </div>

      <div v-if='getResponse.sparam.S22' class='row'>
        <div class='input-group'>
          <span class='input-group-text'>S22</span>
          <span class='input-group-text'>Real</span>
          <span class='input-group-text text-primary bg-white'> {{getResponse.result.S22.Real}} </span>
          <span class='input-group-text'>Imag</span>
          <span class='input-group-text text-primary bg-white'> {{getResponse.result.S22.Imag}} </span>
        </div>
      </div>
    </div>

    <div v-else-if='getResponse.cmd == "rq"' class='table'>

      <table class="table border border-primary text-center">
        <thead>
          <tr>
            <th key="freq" scope="col">Frequency</th>
              <th v-if='getResponse.sparam.S11' key="s11r" scope="col">S11/Real</th>
              <th v-if='getResponse.sparam.S11' key="s11i" scope="col">S11/Imag</th>
              <th v-if='getResponse.sparam.S12' key="s12r" scope="col">S12/Real</th>
