              <th v-if='getResponse.sparam.S21' key="s21r" scope="col">S21/Real</th>
              <th v-if='getResponse.sparam.S21' key="s21i" scope="col">S21/Imag</th>
              <th v-if='getResponse.sparam.S22' key="s22r" scope="col">S22/Real</th>
              <th v-if='getResponse.sparam.S22' key="s22i" scope="col">S22/Image</th>
          </tr>
        </thead>
        <tr v-for="(row, index) in getResponse.result" :id="index" :key="index">
            <td>{{ getFrequencies[index]}}</td>
            <td v-if='getResponse.sparam.S11'>{{row.S11.Real}}</td>
            <td v-if='getResponse.sparam.S11'>{{row.S11.Imag}}</td>
            <td v-if='getResponse.sparam.S12'>{{row.S12.Real}}</td>
            <td v-if='getResponse.sparam.S12'>{{row.S12.Imag}}</td>
            <td v-if='getResponse.sparam.S21'>{{row.S21.Real}}</td>
